import React from 'react';
import styled from 'styled-components'
import theme from '@style/theme'
//import { useCVHighlightQuery } from '../query/highlightquery';

const Highlight = (props) => {
    return(
        <HighlightBox>
            {props.data.allCvHighLightJson.edges.filter(fortype => fortype.node.type === props.selectType).map(({ node, i }) => (
                    <div key={i}>
                        <Sum>{node.sum}</Sum>
                        {node.span ? (
                            <LsIntro>{node.span}</LsIntro>
                        ) : (
                            ''
                        )}
                        {node.list ? (
                            <LsSubIntro>{node.list}</LsSubIntro>
                        ) : (
                            ''
                        )}
                    </div>
                ))
            }
        </HighlightBox>
    )
}

export default Highlight;

const HighlightBox = styled.div`
    margin:10px 0;
    padding: 10px 0;
    @media (max-width: ${theme.screen.md}){
        display: none;
    }
`
const Sum = styled.div`
    color: ${theme.color.grayscale.S1000};
    ${theme.fontSize.lg};
    margin: 10px 0;
`
const LsIntro = styled.div`
    ${theme.fontSize.sm};
    ::before{
        color: ${theme.color.grayscale.S800};
        padding-right: 5px;
    }
`
const LsSubIntro = styled.div`
    ${theme.fontSize.sm};
    color: ${theme.color.grayscale.S700};
    ::before{
        content: "⁃";
        padding-right: 5px;
    }
`
    