export const pageQuery = graphql`
  query {
    allCvJson {
        totalCount
        edges {
            node {
                title
                yrStart
                yrEnd
                location
                org
                type
                desc
            }
        }
    }
    allCvHighLightJson {
      edges {
          node {
              type
              span
              sum
              list
              heading
          }
      }
  }
  }

`


